.Calculator__switchContainer__3PSZj Label {
    margin: auto;
    z-index: 10;
    -webkit-tap-highlight-color: transparent;
    display: block;
    position: relative;
    box-sizing: border-box;
    width: 60px;
    height: 28px;
    border-radius: 20px;
    box-shadow: inset 0 1px 2px 0 rgba(0,0,0,0.5);
    cursor: pointer;
    -webkit-transition: all .25s ease-in-out;
    transition: all .25s ease-in-out;
}

.Calculator__switchContainer__3PSZj Label::before {
    display: block;
    box-sizing: border-box;
    content: '';
    position: absolute;
    top: 1px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,0.5);
    -webkit-transition: all .25s ease-in-out;
    transition: all .25s ease-in-out;
    background: white;
}

